/*-----------------------------Reveal Animation---------------------------------*/
.has-animation {
  position: relative; }
  .has-animation img,
  .has-animation .anim-img {
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 10;
    overflow: hidden;
    object-fit: cover;
    font-family: 'object-fit: cover;'; }
  .has-animation.animate-in:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 0;
    height: 100%;
    z-index: 9;
    background-color: #ddd; }
  .has-animation.animation-ltr.animate-in:before {
    animation: revealLTR 0.6s cubic-bezier(0.4, 0, 0, 1) forwards !important; }
  .has-animation.animation-ltr.animate-in img,
  .has-animation.animation-ltr.animate-in .anim-img {
    animation: revealRTL 0.6s 0.6s cubic-bezier(0.4, 0, 0, 1) forwards !important; }
  .has-animation.animation-rtl.animate-in:before {
    animation: revealRTL 0.6s cubic-bezier(0.4, 0, 0, 1) forwards !important; }
  .has-animation.animation-rtl.animate-in img,
  .has-animation.animation-rtl.animate-in .anim-img {
    animation: revealLTR 0.6s 0.6s cubic-bezier(0.4, 0, 0, 1) forwards !important; }

@keyframes revealRTL {
  0% {
    width: 0;
    right: 0; }
  100% {
    width: 100%;
    right: 0; } }

@keyframes revealLTR {
  0% {
    width: 0;
    left: 0; }
  100% {
    width: 100%;
    left: 0; } }

@keyframes run {
  0% {
    top: -50%; }
  100% {
    top: 110%; } }

@keyframes ripple {
  0% {
    box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0); }
  50% {
    box-shadow: 0px 0px 0px 12px rgba(0, 0, 0, 0.1); }
  100% {
    box-shadow: 0px 0px 0px 12px rgba(0, 0, 0, 0); } }

.make-reveal {
  position: relative;
  overflow: hidden;
  width: 100%;
  display: block; }
  .make-reveal:before {
    content: '';
    height: 100%;
    display: block;
    position: absolute;
    opacity: 1;
    top: 0;
    animation: makeReveal 1.2s 0.1s ease;
    z-index: 2; }

.reveal-block {
  opacity: 0;
  animation-duration: 0.1s;
  animation-delay: 0.6s;
  animation-fill-mode: both;
  z-index: 1;
  height: 100%;
  width: 100%; }

@keyframes makeReveal {
  0% {
    transform-origin: 0% 100%;
    width: 0; }
  50% {
    width: 100%;
    transform-origin: 100% 0%; }
  100% {
    width: 0; } }

